import { Controller, Node } from '@ali/4ever-cangjie';

const getListItemsBySelection = (controller: Controller) => {
  const { document } = controller.value;
  const { listSelections } = controller.value.data;
  if (listSelections.length) {
    return listSelections.reduce((acc: Node[], cur: string) => {
      const n = document.getNode(cur);
      if (n) {
        acc.push(n);
      }
      return acc;
    }, []);
  }
  return [];
}

export default getListItemsBySelection;